:root {
  --basesize: 1.25rem;
  --bigsize: calc(4vw + 2vh + .5vmin);
  --mediumsize: calc(var(--bigsize) / 2);
  --introsize:1.5em;
  --delay: 10ms;
  --indent: 6rem;
  --basefamily:  "Times", "Times New Roman", serif;
  --margin:  calc(var(--bigsize) * 2 + var(--mediumsize)); }

@media (max-width: 1400px) {
  :root {
    --introsize:1.25em; } }

@media (max-width: 850px) {
  :root {
    --mediumsize: var(--bigsize);
    --margin: calc( var(--bigsize) + var(--mediumsize) ); } }

@media (max-width: 500px) {
  :root {
    --basesize: 1rem;
    --indent: 3rem
  ; } }

*, *::before, *::after {
  box-sizing: border-box; }

body {
  margin: 0;
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

a {
  color: currentColor;
  text-decoration: none; }

html {
  scroll-behavior: smooth; }

@media (prefers-reduced-motion: reduce) {
  html {
    scroll-behavior: auto; } }

.marquee {
  display: block;
  position: relative;
  overflow: hidden;
  min-height: 1.4em;
  margin-bottom: 1em;
  font-style: italic; }

.marquee span {
  display: inline-block;
  animation: marquee 12s linear infinite;
  position: absolute;
  white-space: nowrap; }

@keyframes marquee {}#header span {
  position: fixed;
  font-size: var(--bigsize); }

.r {
  writing-mode: vertical-rl;
  transform: rotate(-180deg);
  bottom: var(--bigsize);
  left: var(--bigsize);
  margin-left: -.75em; }

.w {
  top: var(--bigsize);
  left: var(--bigsize);
  margin-top: -.75em;
  margin-left: -.35em; }

.d {
  right: var(--bigsize);
  top: var(--bigsize);
  margin-top: -.75em; }

nav {
  margin: var(--margin) var(--margin) 0 var(--margin); }
  nav ul {
    margin-bottom: 1rem; }
  nav .themes {
    max-width: 22em;
    font-size: var(--mediumsize);
    padding: 0;
    list-style-type: none;
    line-height: 1.2em; }
    nav .themes a {
      color: currentColor;
      height: 1.1em;
      text-shadow: -2px -2px 0 #fff, 2px -2px 0 #fff, -2px 2px 0 #fff, 2px 2px 0 #fff;
      display: inline-block;
      background-image: linear-gradient(to top, transparent, transparent 1px, black 1px, black 3px, transparent 3px); }
    nav .themes .active a {
      font-style: italic; }
  nav li {
    display: inline-block; }
  nav .languages {
    text-transform: uppercase; }

.list {
  --padding-left:10vw;
  padding-left: var(--padding-left);
  position: relative; }
  @media (max-width: 850px) {
    .list {
      padding-left: 0; } }
  .list h2 {
    font-size: 1em;
    position: absolute;
    left: 0;
    top: 0;
    margin: 0; }
    @media (max-width: 850px) {
      .list h2 {
        position: static;
        margin-bottom: 1em;
        font-size: calc(var(--bigsize) / 1.15); } }
  .list article {
    margin-bottom: 1em; }
  .list h3, .list h4 {
    margin: 0;
    font-size: 1em; }
  .list p, .list blockquote {
    text-indent: 8ch; }
  .list .lang {
    font-size: .75em;
    vertical-align: top; }

.theme-introduction {
  font-size: 1.5em;
  max-width: calc(43ch + var(--padding-left));
  margin-bottom: 1em;
  line-height: 1.25; }

.action {
  display: inline-block;
  margin-bottom: 1em; }
  .action span:first-child {
    display: inline; }
  .action span:last-child {
    display: none; }

.action-checkbox {
  display: none; }
  .action-checkbox:checked + label span:first-child {
    display: none; }
  .action-checkbox:checked + label span:last-child {
    display: inline; }
  .action-checkbox:checked ~ .list .details {
    display: none; }

.article-header {
  margin-bottom: 2em; }

.article-meta {
  color: #444;
  font-size: .85em;
  display: flex;
  flex-wrap: wrap; }
  .article-meta *:not(:last-child) {
    margin-right: .75em; }

.article-introduction {
  font-size: var(--introsize);
  max-width: calc(65ch / 1.5);
  margin-bottom: 1em;
  line-height: 1.25; }
  @media (max-width: 1400px) {
    .article-introduction {
      max-width: calc(65ch / 1.25); } }

.article-text p + p,
.article-text blockquote + p {
  text-indent: var(--indent); }

.article-text blockquote {
  margin: 1em 0;
  font-size: var(--introsize);
  padding-left: var(--indent);
  font-style: italic; }
  .article-text blockquote em, .article-text blockquote i {
    font-style: normal; }

.article-text ul {
  list-style-type: disc;
  list-style-position: inside; }

.article-text .initials {
  font-variant: all-small-caps; }

.article-text .question {
  padding-left: var(--indent); }

.article-text .answer + .question {
  margin-top: 1em; }

footer {
  font-size: .85em;
  color: #444;
  margin: 0 var(--margin) var(--margin) var(--margin); }
  footer a:hover {
    color: black; }

#contribute {
  position: relative; }
  #contribute::before {
    content: "☺";
    font-size: 2em;
    position: absolute;
    left: -1.5em;
    opacity: 0; }

#contribute:target::before {
  animation: highlight 500ms ease-out 1s forwards; }

@keyframes highlight {
  1% {
    opacity: 1; }
  100% {
    transform: rotate(720deg);
    opacity: 1; } }

main {
  margin: var(--margin) var(--margin) 0 var(--margin); }

@media (max-width: 850px) {
  main, nav, footer {
    margin: var(--margin) var(--bigsize) 0 calc(var(--bigsize) * 2); }
  footer {
    margin-bottom: var(--bigsize); } }
